<style scoped lang="less">
    .pages {
        text-align: left;
        padding: 0 20px;
        .form {
            width: 40%;
            margin-top: 30px;
        }
    }

</style>


<template>
    <div class="pages">
        <H3>基础信息</H3>
        <div class="line"></div>
        <el-form class="form" ref="form" :model="form" label-width="120px">
            <el-row>
                <el-form-item label="分组名称">
                    <el-input v-model="form.name" placeholder="请输入"></el-input>
                </el-form-item>
                <el-form-item label="分组排序">
                    <el-input type="number" v-model="form.sort" placeholder="请输入"></el-input>
                </el-form-item>
            </el-row>
            <el-form-item>
                <el-button type="primary" :loading="isLoading" @click="onSubmit">提交</el-button>
                <el-button @click="back">返回</el-button>
            </el-form-item>
        </el-form>
    </div>
</template>

<script>
    import api from '../../js/api/member/group'

    export default {
        data() {
            return {
                form: {
                    name: '',
                    sort: null,
                },
                industry: [],
                role: [],
                isLoading: false,
                accountIsEdit: false,
            }
        },
        mounted() {
            this.init();

        },
        methods: {
            init() {
                if (this.$route.query.id !== undefined) {
                    this.loadData()
                }
            },
            onSubmit() {
                this.isLoading = true;
                api.save(this.form, () => {
                    this.isLoading = false;
                    this.$message.success('保存成功!');
                    this.$router.push({name: 'group'});
                    this.$route.meta.keepAlive = false;
                }, () => {
                    this.isLoading = false;
                })
            },

            loadData() {
                api.detail({id: this.$route.query.id}, res => {
                    this.form = res.data.data;
                })
            },
            back() {
                this.$router.back();
            },
            edit() {
                this.isNoEdit = false;
            }
        }
    }
</script>
